/* eslint-disable */
import { View } from '@tarojs/components'
import { Button } from '@antmjs/vantui'

Button.config({
  asyncLoading: true, // clickloading
})

export default function Demo() {
  // Promiseloading
  const handle = async () => {
    await timeout()
  }

  return (
    <View>
      <Button type="default"></Button>
      <Button type="primary"></Button>
      <Button type="info"></Button>
      <Button type="warning"></Button>
      <Button type="danger"></Button>
      <View></View>
      <Button type="info" onClick={handle} loadingText="...">
        
      </Button>
      <Button
        type="primary"
        onClick={handle}
        loadingMode="toast"
        loadingText="..."
        loadingType="spinner"
      >
        
      </Button>
    </View>
  )
}

function timeout() {
  return new Promise((resolve) => {
    setTimeout(() => {
      resolve(1)
    }, 3000)
  })
}
